import {handleActions} from 'redux-actions'
import * as TYPES from "../constants/actions"

export default   handleActions({
  [TYPES.GET_TEMPLATES]: (state, result) => {
    if(!result.error && result.payload && result.payload.data.success){
      let end = Object.assign({},state,{templatesInfo: result.payload.data.data})
      return end
    }
    return state

  },
 [TYPES.GET_MODULE_TEXT]: (state, result) => {
   if(!result.error && result.payload && result.payload.data.success){
      let end = Object.assign({},state,{textData: result.payload.data.data})
      return end
    }
    return  Object.assign({},state,{textData: '没有获取到此模块的文字信息,请点击重试'})
  },
  [TYPES.SAVE_TEXTAREA]: (state, result) => {
    if(!result.error && result.payload){
      let end = Object.assign({},state,{textData: result.payload})
      return end
    }
    return state
  },



  DECREMENT: (state, action) => ({
    counter: state.counter - action.payload
  })
}, {templatesInfo: []});


