import React from 'react';
import { Switch,Route,HashRouter} from 'react-router-dom';
import NoFound from './containers/noFound';
import TemplateConfig from './containers/templateConfig';
import style from './style.scss'
import GlobalBase from "./components/globalBase"

export default  ({ match }) => {
  return   <div>
    <div className={style.container} >
      <div className="left">
        <aside id="aside" class="aside">
          <div class="logo">运营平台</div>
          <ul class="menu" id="asidemenu">
          </ul>
        </aside>
      </div>
      <div className="right">
        <GlobalBase/>
        <HashRouter>
            <Switch>
              <Route path="/hospital/:id" exact component={TemplateConfig}/>
              <Route path="/" component={NoFound}/>
            </Switch>
        </HashRouter>
      </div>
    </div>
  </div>
}


