import React from 'react';
import style from './style.scss';
import _ from 'lodash';
import Icon from 'antd/lib/icon';
import Menu from 'antd/lib/menu';
import Alert from 'antd/lib/alert';
import Input from 'antd/lib/input';
import Button from 'antd/lib/button';
import Notification from 'antd/lib/notification';


const { TextArea } = Input;
export default class EditModule extends  React.Component {
  state={
    loading: false
  }
  componentDidMount(){
    let {module} = this.props
    this.props.getModuleText({
      hosId:this.props.HospitalId,
      pageId:module.id
    })
  }
  componentWillReceiveProps(nextProps){
    if(parseInt(this.props.module.id ) !==  parseInt(nextProps.module.id)){
      console.log('更新module id')
      let {module} = nextProps
      this.props.getModuleText({
        hosId:this.props.HospitalId,
        pageId:module.id
      })
    }
  }
  updateTextarea(e){
    let value = e.target.value
    console.log(value)
    this.props.saveTextArea(value)
  }
  syncTextArea(){
    this.setState({loading: true})
    let {module} = this.props
    let result = this.props.syncTextArea({
      hosId:this.props.HospitalId,
      pageId:module.id,
      sections:this.props.textData
    })

    result.then((res)=>{
      if(res.payload){
        this.setState({loading: false});
        this.props.templateModifyFalse();
        Notification.success({
          message: '同步模块文字成功！'
        });
      }else{
        this.setState({loading: false});
        Notification.error({
          message: '同步失败！',
          description: '服务当前不可用'

        });
      }
    })

  }
  render(){
    let textData = this.props.textData ? this.props.textData : "这个模块还没有文本数据！"
    return <div>
      <Alert message={`当前编辑的模板ID是 ${this.props.templateID},模块名是${this.props.module.name}`} type="info" showIcon />
      <Menu
        mode="horizontal"
        selectedKeys={["edit"]}
      >
        <Menu.Item key="edit">
          <Icon type="mail" />编辑模块
        </Menu.Item>
        <Menu.Item key="preview">
          <Icon type="mail" />预览
        </Menu.Item>
        <Menu.Item key="gpdf">
          <a href="https://ant.design" target="_blank" rel="noopener noreferrer"><Icon type="mail" />生成PDF</a>
        </Menu.Item>
      </Menu>

      <div className={style.edit_body}>
        <TextArea placeholder="正在加载文本信息..."  value={textData}  rows={8} onChange={this.updateTextarea.bind(this)}/>
        <div className={style.sync_circle}>
          <Button type="primary" icon="sync" onClick={this.syncTextArea.bind(this)} loading={this.state.loading}>同步模块文字</Button>
        </div>
      </div>
    </div>
  }
}



