import axios from 'axios'
import * as TYPES from "../constants/actions"
import store from '../store'

axios.defaults.baseURL = 'http://121.42.187.170/autoreport';



//request cancel loading
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  store.dispatch({
    type:"REQUEST_BEGIN"
  })
  return config;
}, function (error) {
  return Promise.reject(error);
});

//response reacord loading
axios.interceptors.response.use(function (config) {

  // Do something before request is sent
  store.dispatch({
    type:"REQUEST_COMPLETE"
  })
  console.log(12)
  if(!config.data.success){
    store.dispatch({
      type:"ERROR_OCCUR",
      payload:"数据获取失败!"
    })
  }
  return config;
}, function (error) {
  let errorDetail = error.response.data
  store.dispatch({
    type:"REQUEST_COMPLETE"
  })
  store.dispatch({
    type:"ERROR_OCCUR",
    payload:"请求发生了错误!"
  })


});


export default {
  [TYPES.GET_TEMPLATES]: (params) => ({
    type: TYPES.GET_TEMPLATES,
    payload: axios.get('/template',{params})
  }),
  [TYPES.SYNC_MODULES_IN_TEMPLATES]: (params) => ({
    type: TYPES.SYNC_MODULES_IN_TEMPLATES,
    payload: axios.post('/template/update',params)
  }),
  [TYPES.GET_MODULE_TEXT]: (params) => ({
    type: TYPES.GET_MODULE_TEXT,
    payload: axios.get('/module',{params})
  }),
    [TYPES.SAVE_TEXTAREA]: (params) => ({
    type: TYPES.SAVE_TEXTAREA,
    payload: params
  }),
  [TYPES.SYNC_TEXTAREA]: (params) => ({
    type: TYPES.SYNC_TEXTAREA,
    payload: axios.post('/module/update',params)
  }),
  [TYPES.ADD_NEW_MODULE]: (params) => ({
    type: TYPES.ADD_NEW_MODULE,
    payload: axios.post('/module/insert',params)
  })
}




